l notice they're good at what they do and snipe them. How do we do this? Sweeping assets and giving them to the people who keep the community alive - either for free, or at a discount through exclusive auctions and minigames. The people who pump everyone elses' bags deserve to be given some bags of their own. ")]),_c('h3',[_vm._v("4. Phase 2: DAO as a nexus:")]),_c('br'),_c('p',[_vm._v(" There's a second social arbitrage the DAO wants to solve. Powerautists often have genius ideas they're unable to execute - autism rarely exists alongside business instincts. The DAO will act as a hub to connect community powerautists with people who can build out their ideas. When these projects approach launch, holders get exclusive access to seed rounds. ")]),_c('br'),_c('h3',[_vm._v("5. “we like the cults”")]),_c('br'),_c('p',[_vm._v(" Since the dawn of the internet, authentic subcultures have been emerging. These communities are the authentic radicals, the free thinkers and the self-taught creators. Those that lean into absurdity or create something new with integrity, rather than repeating and replaying with a reliance on marketing money. YEET wants to recognise, support and connect those who build culture, allowing them to grow their finances too. ")])]):_c('div',[_c('p',{staticStyle:{"margin-left":"20px","margin-top":"20px"}},[_vm._v(" Password Protected ")]),_c('p',{staticStyle:{"color":"red","margin-left":"20px"}},[_vm._v(" "+_vm._s(_vm.showAboutIncorrect ? "Incorrect..." : "")+" ")]),_c('div',{staticStyle:{"display":"flex","flex-direction":"row","margin":"20px"}},[_c('v-text-field',{attrs:{"outlined":"","single-line":"","dense":"","error":_vm.showAboutIncorrect ? true : false,"type":"password"},on:{"keypress":function($event){if(!$event.type.indexOf('key')&&_vm._k($event.keyCode,"enter",13,$event.key,"Enter"))return null;return _vm.checkAboutpass.apply(null, arguments)}},model:{value:(_vm.aboutpass),callback:function ($$v) {_vm.aboutpass=$$v},expression:"aboutpass"}}),_c('